<template>
	<div class="container">
		<!-- 		<uni-nav-bar title="EmoVision" color="#00aaff" left-icon="back" click-left=""></uni-nav-bar> -->
		<NavBar></NavBar>
		<div class="title">请选择模版</div>
		<div class="buttons">
			<div class="btn" v-for="template in templates" :key="template.name" @click="selectTemplate(template.name)">
				<img :src="template.img" :alt="template.name">
			</div>
		</div>
		<div class="footer">
			<div class="selected">{{ selectedText }}</div>
			<div class="confirm" @click="confirmSelection">确认</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				templates: [{
						name: '空模版',
						img: 'static/c1.png'
					}, {
						name: '山水图',
						img: 'static/c2.png'
					},
					{
						name: '花草图',
						img: 'static/c3.png'
					},
					{
						name: '田园图',
						img: 'static/c4.png'
					}
				],
				selectedTemplate: ''
			};
		},
		computed: {
			selectedText() {
				return this.selectedTemplate ? `已选择${this.selectedTemplate}模版` : '未选择';
			}
		},
		methods: {
			selectTemplate(templateName) {
				this.selectedTemplate = this.selectedTemplate === templateName ? '' : templateName;
			},
			confirmSelection() {
				uni.redirectTo({
					url: '/pages/new_draw/new_draw?stage=1'
				})
			}
		},
		onLoad() {
			uni.hideNavigationBarLoading();
		}
	};
</script>

<style scoped>
	@import './model_select.css'
</style>